import React from 'react'
import '../carrouselVerti.css'


function CarrouselVerti(){
    
    return(
        <section id="slideshow">
		
        <div class="container">
            <div class="c_slider"></div>
            <div class="slider">
                <figure class="slidin">
                    <img src="img/dummy-640x310-1.jpg" alt="" width="640" height="310" />
                    <figcaption>The mirror of soul</figcaption>
    
                </figure>
                <figure class="slidin">
                    <img src="img/dummy-640x310-2.jpg" alt="" width="640" height="310" />
                    <figcaption>Let's cross that bridge when we come to it</figcaption>
                </figure>
               <figure class="slidin">
                    <img src="img/dummy-640x310-3.jpg" alt="" width="640" height="310" />
                    <figcaption>Sushi<em>(do)</em> time</figcaption>
    
                </figure>
                <figure class="slidin">
                    <img src="img/dummy-640x310-4.jpg" alt="" width="640" height="310" />
                    <figcaption>Waking Life</figcaption>
                </figure>
            </div>
        </div>
            
        <span id="timeline"></span>
    </section>
    )
}

export default CarrouselVerti